import React, { Component } from "react";

// styles
import styled from "styled-components";
// import * as desktop from "./styles/desktop";
// import * as mobile from "./styles/mobile";

import { Gooey } from "../../../../images/SVG/effects";

class MainPage extends Component {
	render() {
		const Fragment = React.Fragment;
		return (
			<Fragment>
				<h3 style={{ padding: "30px" }}></h3>
				<ol style={{ listStyle: "decimal", paddingLeft: "30px" }}>
					<li>     </li>
					<li>   </li>
					<li> 404 </li>
					<li>  </li>
					<li>     </li>
					<li>    (mobile - desktop).. ..</li>
					<li>     </li>
					<li>     </li>
					<li>Low Quality Image Placeholders</li>
					<li> shouldComponentUpdate   </li>
					<li>     ?</li>
					<li>  </li>
					<li>  </li>
					<li> -  ((</li>
					<li>    </li>
					<li>  !!</li>
					<li>  </li>
				</ol>
				<BallsWrapper>
					<Ball1 />
					<Ball2 />
				</BallsWrapper>
				<Gooey />
			</Fragment>
		);
	}
}

const BallsWrapper = styled.div`
	width: 700px;
	filter: url("#goo");
`;

const Ball1 = styled.div`
	display: inline-block;
	width: 100px;
	height: 100px;
	border-radius: 50%;
	background-color: #fff;
	margin: 30px;
`;

const Ball2 = styled.div`
	display: inline-block;
	width: 100px;
	height: 100px;
	border-radius: 50%;
	background-color: #fff;
	margin: 30px;
	animation: anim 2s infinite;

	@keyframes anim {
		0% {
			transform: translateX(-150px);
		}

		50% {
			transform: translateX(100px);
		}

		100% {
			transform: translateX(-150px);
		}
	}
`;

export default MainPage;
