import {
	OPEN_PRODUCT_ITEM_MODAL,
	GET_PRODUCT_ITEM_FULL_DESCRIPTION,
	LOADING_ITEM_FULL_DESCRIPTION,
	PRODUCT_ITEM_SERVER_ERROR
} from "../actions/types";
import axios from "axios";

export const getProductItemFullDescription = productId => dispatch => {
	dispatch(loading());

	setTimeout(() => {
		axios({
			method: "post",
			url: "/api/goods/product-item",
			timeout: 5000,
			data: { productId }
		})
			.then(({ data }) => {
				dispatch({ type: GET_PRODUCT_ITEM_FULL_DESCRIPTION, fullDescription: data, loading: false });
			})
			.catch(() => {
				dispatch(serverError("!  !"));
			});
	}, 3000);
};

export const openModal = readyToStartModalAnimation => {
	return { type: OPEN_PRODUCT_ITEM_MODAL, readyToStartModalAnimation };
};

const loading = ok => {
	let loading = new Promise(resolve => {
		resolve();
	});

	return {
		type: LOADING_ITEM_FULL_DESCRIPTION,
		loading
	};
};

const serverError = error => {
	return {
		type: PRODUCT_ITEM_SERVER_ERROR,
		error
	};
};
