import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { Cookies } from "react-cookie";

// eslint-disable-next-line no-unused-vars
import regeneratorRuntime from "regenerator-runtime";

// styles
import { CSSTransition } from "react-transition-group";
import styled from "styled-components";
import * as desktop from "./styles/desktop";
import * as mobile from "./styles/mobile";

// icons
import { IconConstructor, closelIcon, leftArrowIcon, rightArrowIcon, spinner } from "../../../images/SVG/icons.js";

// SVG effects
import { Gooey } from "../../../images/SVG/effects";

// actions
import { getProductItemFullDescription } from "../../../actions/productItemActions";

//utils
import { shoppingCart } from "../../../utils/shopping_cart/";

const SERVER_URL = process.env.NODE_ENV === "production" ? process.env.SERVER_URL : "http://localhost:5000";

class Modal extends Component {
	constructor(props) {
		super(props);

		this.modalWindow = React.createRef();
		this.imageBall = React.createRef();
		this.descriptionBall = React.createRef();
		this.otherBall = React.createRef();

		this.state = {
			alreadyInCart: shoppingCart.check(props.match.params.productId, props.cookies),
			imageNumber: 0,
			imageAmount: 0,
			darkScreen: false,
			modal: false,
			loading: true,
			showContent: false
		};
	}

	static propTypes = {
		getProductItemFullDescription: PropTypes.func,
		sendToCart: PropTypes.func,
		fullDescription: PropTypes.object,
		match: PropTypes.object,
		history: PropTypes.object,
		readyToStartModalAnimation: PropTypes.object,
		cookies: PropTypes.instanceOf(Cookies),
		loading: PropTypes.bool,
		error: PropTypes.string
	};

	async componentDidMount() {
		// redux props
		const { readyToStartModalAnimation } = this.props;

		// redux actions
		const { getProductItemFullDescription } = this.props;

		this.setState({
			darkScreen: true
		});

		getProductItemFullDescription(this.props.match.params.productId);

		await Promise.resolve(readyToStartModalAnimation).then(res => (this.end = res));

		this.setState({
			modal: true
		});

		this.animationStepCounter = (() => {
			let step = 1;
			return () => step++;
		})();

		this.nextAnimation();
	}

	static getDerivedStateFromProps(nextProps, prevState) {
		const alreadyInCart = shoppingCart.check(nextProps.match.params.productId, nextProps.cookies);

		if (alreadyInCart !== prevState.alreadyInCart) {
			return {
				alreadyInCart
			};
		}

		if (Object.keys(nextProps.fullDescription).length) {
			return {
				imageAmount: nextProps.fullDescription.img[1].length
			};
		} else {
			return null;
		}
	}

	onPreviousImage = () => {
		// state
		let { imageAmount, imageNumber } = this.state;

		if (imageAmount && imageNumber > 0) {
			this.setState({
				imageNumber: --imageNumber
			});
		}
	};

	onNextImage = () => {
		// state
		let { imageAmount, imageNumber } = this.state;

		if (imageAmount && imageNumber < imageAmount - 1) {
			this.setState({
				imageNumber: ++imageNumber
			});
		}
	};

	onCloseModal = () => {
		this.setState({
			showContent: false
		});

		this.animationStepCounter = (() => {
			let step = -1;
			return () => step--;
		})();
	};

	onSendToCart = () => {
		// redux props
		const { productId } = this.props.fullDescription;

		// redux actions
		const { cookies } = this.props;

		navigator.cookieEnabled ? shoppingCart.add(productId, cookies) : this.onDisplayCookiesWarning();
	};

	onDisplayCookiesWarning = () => {
		alert(" !");
	};

	onDefaultState = () => {
		this.setState({
			imageNumber: 0,
			imageAmount: 0
		});
	};

	nextAnimation = () => {
		const step = this.animationStepCounter();

		if (step > 0) {
			switch (step) {
				case 1: {
					this.imageBall.current.classList.add("loading-position");
					this.descriptionBall.current.classList.add("loading-position");
					this.otherBall.current.classList.add("loading-position");
					break;
				}
				case 2: {
					this.otherBall.current.addEventListener("animationend", this.nextAnimation);

					this.modalWindow.current.classList.add("hide-loading-ball");
					this.imageBall.current.classList.add("to-position");
					this.descriptionBall.current.classList.add("to-position");
					this.otherBall.current.classList.add("to-position");
					break;
				}
				default: {
					this.setState({
						showContent: true
					});
					this.otherBall.current.removeEventListener("animationend", this.nextAnimation);

					this.imageBall.current.classList.add("show-content");
					this.descriptionBall.current.classList.add("show-content");
					this.otherBall.current.classList.add("show-content");
				}
			}
		} else {
			switch (step) {
				case -1: {
					this.otherBall.current.addEventListener("animationend", this.nextAnimation);

					this.modalWindow.current.classList.add("show-loading-ball");
					this.imageBall.current.classList.add("back-to-center");
					this.descriptionBall.current.classList.add("back-to-center");
					this.otherBall.current.classList.add("back-to-center");
					break;
				}
				default: {
					this.otherBall.current.removeEventListener("animationend", this.nextAnimation);
					this.setState({
						darkScreen: false
					});
					if (this.end) this.end();
				}
			}
		}
	};

	asd = () => {
		if (this.state.loading) {
			this.setState({
				loading: false
			});

			this.nextAnimation("forward");
		}
	};

	render() {
		const Fragment = React.Fragment;

		// redux props
		const { error } = this.props;
		const { img, title, body, category, productId, price } = this.props.fullDescription;

		// state
		const { darkScreen, modal, alreadyInCart, imageAmount, imageNumber, loading, showContent } = this.state;

		return (
			<CSSTransition
				in={darkScreen}
				classNames={"product-item-modal"}
				timeout={250}
				onExited={() => {
					this.onDefaultState();

					this.props.history.push("/goods");
				}}
				unmountOnExit
			>
				<ModalContainer>
					{modal && (
						<ModalWindow ref={this.modalWindow} onClick={this.asd}>
							<ImageBall ref={this.imageBall}>
								{showContent && (
									<ImageContainer>
										{img && <Img key={"full-image-" + productId} src={SERVER_URL + img[1][imageNumber]} />}
										<PreviousImageButton onClick={this.onPreviousImage}>
											<LeftArrowIcon />
										</PreviousImageButton>
										<NextImageButton onClick={this.onNextImage}>
											<RightArrowIcon />
										</NextImageButton>
									</ImageContainer>
								)}
							</ImageBall>

							<DescriptionBall ref={this.descriptionBall}>
								<CSSTransition
									in={showContent}
									classNames={"description"}
									timeout={250}
									onExited={() => {
										this.nextAnimation();
									}}
									unmountOnExit
								>
									<Main>
										<Description>{body}</Description>
									</Main>
								</CSSTransition>
							</DescriptionBall>

							<OtherBall ref={this.otherBall}>
								{showContent && (
									<Fragment>
										<CloseButton onClick={this.onCloseModal}>
											<CloselIcon />
										</CloseButton>
										<Header>
											<Title>{title}</Title>

											{!error && (
												<Fragment>
													<Category>: {category}</Category>
													<Category> : {productId}</Category>
												</Fragment>
											)}
										</Header>

										<Footer>
											<Price>{price ? `: ${price}` : " "}</Price>
											<BuyButton onClick={this.onSendToCart} alreadyInCart={alreadyInCart}>
												{alreadyInCart ? "  " : " "}
											</BuyButton>
										</Footer>
									</Fragment>
								)}
							</OtherBall>
							{loading && <LoadingIcon />}

							{/* 
						{loading ? (
							<LoadingIcon />
						) : (
							<Fragment>

								{error ? (
									<Attention>{error}</Attention>
								) : (
									<Fragment>
			
										<ImageCounter>
											{imageNumber + 1} - {imageAmount}
										</ImageCounter>

		
									</Fragment>
								)}
							</Fragment>
						)} */}
							<Gooey />
						</ModalWindow>
					)}
				</ModalContainer>
			</CSSTransition>
		);
	}
}

const mapStateToProps = store => ({
	readyToStartModalAnimation: store.productItem.readyToStartModalAnimation,
	fullDescription: store.productItem.fullDescription,
	loading: store.productItem.loading,
	error: store.productItem.error
});

export default connect(
	mapStateToProps,
	{ getProductItemFullDescription }
)(Modal);

// styled components -------------------------

const ModalContainer = styled.div`
	${props => (props.theme.desktop ? desktop.modalContainer : mobile.modalContainer)};
`;

const ModalWindow = styled.section`
	${props => (props.theme.desktop ? desktop.modalWindow : mobile.modalWindow)};
`;

const ImageBall = styled.div`
	${props => (props.theme.desktop ? desktop.imageBall : mobile.imageBall)};
`;

const DescriptionBall = styled.div`
	${props => (props.theme.desktop ? desktop.descriptionBall : mobile.descriptionBall)};
`;
const OtherBall = styled.div`
	${props => (props.theme.desktop ? desktop.otherBall : mobile.otherBall)};
`;

const LoadingIcon = styled(IconConstructor).attrs({
	body: spinner,
	viewBox: "0 0 16 16"
})`
	${props => (props.theme.desktop ? desktop.loadingIcon : mobile.loadingIcon)};
`;

const Header = styled.header`
	${props => (props.theme.desktop ? desktop.header : mobile.header)};
`;

const Title = styled.p`
	${props => (props.theme.desktop ? desktop.title : mobile.title)};
`;

const Category = styled.p`
	${props => (props.theme.desktop ? desktop.category : mobile.category)};
`;

const CloselIcon = styled(IconConstructor).attrs({
	body: closelIcon
})`
	${props => (props.theme.desktop ? desktop.closelIcon : mobile.closelIcon)};
`;

const CloseButton = styled.div`
	${props => (props.theme.desktop ? desktop.closeButton : mobile.closeButton)};
`;

const Main = styled.div`
	${props => (props.theme.desktop ? desktop.main : mobile.main)};
`;

const Description = styled.div`
	${props => (props.theme.desktop ? desktop.description : mobile.description)};
`;

const ImageContainer = styled.div`
	${props => (props.theme.desktop ? desktop.imageContainer : mobile.imageContainer)};
`;

const Img = styled.img.attrs({
	alt: " ..."
})`
	${props => (props.theme.desktop ? desktop.img : mobile.img)};
`;

const LeftArrowIcon = styled(IconConstructor).attrs({
	body: leftArrowIcon,
	viewBox: "-8 -8 48 48"
})`
	${props => (props.theme.desktop ? desktop.leftArrowIcon : mobile.leftArrowIcon)};
`;

const RightArrowIcon = styled(IconConstructor).attrs({
	body: rightArrowIcon,
	viewBox: "-8 -8 48 48"
})`
	${props => (props.theme.desktop ? desktop.rightArrowIcon : mobile.rightArrowIcon)};
`;

const PreviousImageButton = styled.div`
	${props => (props.theme.desktop ? desktop.previousImageButton : mobile.previousImageButton)};
`;

const NextImageButton = styled.div`
	${props => (props.theme.desktop ? desktop.nextImageButton : mobile.nextImageButton)};
`;

const ImageCounter = styled.div`
	${props => (props.theme.desktop ? desktop.imageCounter : mobile.imageCounter)};
`;

const Footer = styled.div`
	${props => (props.theme.desktop ? desktop.footer : mobile.footer)};
`;

const Price = styled.div`
	${props => (props.theme.desktop ? desktop.price : mobile.price)};
`;

const BuyButton = styled.div`
	${props => (props.theme.desktop ? desktop.buyButton : mobile.buyButton)};
`;

const Attention = styled.div`
	${props => (props.theme.desktop ? desktop.attention : mobile.attention)};
`;

// -------------------------------------------
