export const imageCounter = css`
	width: 100%;
	text-align: center;
	font-size: 16px;
	color: #959595;
`;

export const footer = css`
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: baseline;
	margin-top: 20px;
`;

export const price = css`
	font-weight: 600;
	font-size: 16px;
`;

export const buyButton = css`
	position: relative;
	width: 200px;
	height: 40px;
	cursor: pointer;
	pointer-events: ${props => (props.alreadyInCart ? "none" : "auto")};
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 16px;
	color: #fff;

	::before {
		position: absolute;
		content: "";
		width: 200px;
		height: 40px;
		background-color: ${props => (props.alreadyInCart ? "#70bc7b" : "#f1592a")};
		border-radius: 5px;
		transition: all ease 100ms;
		z-index: -1;
	}

	:hover ::before {
		transform: scale(1.05, 1.1);
	}
`;

export const attention = css`
	margin: auto;
	font-size: 16px;
	font-weight: 600;
`;
